import {  notification } from 'antd'

const AppNotification = {
    fileSaved :  () => {
        notification.success({
          message: 'Success !',
          description: 'File content Successfully saved :)',
        });
    },
    errorSavingFile: () => {
        notification.error({
          message: 'Error',
          description: 'An error occured while saving file :(',
        });
    }
}

export default AppNotification