import React, { Component, Fragment } from 'react';
import { Layout  } from 'antd';
import { Row, Col } from 'antd';
import { Form, Input } from 'antd';
import { Button } from 'antd'
import { SaveOutlined } from '@ant-design/icons';
import Api from '../utils/Api';
import AppNotification from '../utils/AppNotification';

import AppHeader from './AppHeader';
import DataLoader from './DataLoader';
import './Editor.css';

const { TextArea } = Input;
const { Content } = Layout;
const tailLayout = {wrapperCol: { offset: 17, span: 7 }};




class  Editor extends Component  {
    state = {
        isLoading: true,
        fileContent: 'file content',
    };

    formRef = React.createRef();

    componentDidMount() {
        this.readFileContent().then(value => {
           console.log(value);
           this.setState({
               isLoading: false,
               fileContent: value
           });

           this.formRef.current.setFieldsValue({fileContent: value});
       });
    }

    onFileSave = values => this.updateFileContent(values.fileContent);

    readFileContent = async () => await Api.get('filetoedit').then(res =>  res.data);

    
    updateFileContent = (fileContent) => {
        Api.post('filetoedit', {fileContent: fileContent})
            .then(res => AppNotification.fileSaved())
            .catch(err => AppNotification.errorSavingFile())
    }
    
   
    render() {
        const contentOrLoader = this.state.isLoading ?
                (<DataLoader></DataLoader>):
                (<Fragment>
                    <Row><Col span={24}><div className="file-title">Editing /Filetoedit.js</div></Col></Row>
                    <br/>
                    <Row>
                        <Col span={24}>
                            <Form ref={this.formRef} name="editorform" layout="vertical" onFinish={this.onFileSave}>
                                <Form.Item label="File content (not required)" name="fileContent">
                                    <TextArea autoSize = {{minRows: 9, maxRows: 19}} style={{borderRadius: '6px'}} />
                                </Form.Item>
                                <Form.Item {...tailLayout}>
                                    <Button type="primary" htmlType="submit" style={{height: '38px', borderRadius: '5px'}} block>
                                    <SaveOutlined />
                                    Save file content
                                    </Button>
                                </Form.Item>
                            </Form>
                        </Col>
                    </Row>
                </Fragment>);



        return (
            <Layout className="layout">
                <AppHeader></AppHeader>
                <Content style={{ padding: '30px 0' }}>
                    <section>
                        <div className="editor">
                            { contentOrLoader }
                        </div>
                    </section>
                </Content>
            </Layout>
        )
    }
}

export default Editor;